.ReciterIcon {
    display: inline-block;
    border-radius: 20px;
    margin: 5px;
    height: 40px;
    width: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    button:disabled & {
        filter: grayscale(100%);
    }
}
